<template>
    <div class="box">
        <div class="box-body">
            <div class="row">
                <div class="col-xs-6">
                    <h3 class="box-title">Danh sách tỉnh thành</h3>
                    <input v-on:keyup="search" type="text" class="form-control" placeholder="Tìm kiếm...">
                    <div id="table-province">
                        <table class="table table-hover" >
                            <tr>
                                <th>STT</th>
                                <th>Tên</th>
                                <th>Url</th>
                                <th>Giá</th>
                                <th>Chức năng</th>
                            </tr>
                            <tr v-for="item in province" :key="item.id" :class="{active: item.id==active}">
                                <td>{{ item.stt }}</td>
                                <td>{{ item.name }}</td>
                                <td>{{ item.url }}</td>
                                <td>{{ item.price }}</td>
                                <td>
                                    <a v-on:click="viewDistrict(item.id)" class="btn btn-success">Xem quận huyện</a> |
                                    <router-link :to="{name: 'route.admin.province.edit', params:{id: item.id}}" class="btn btn-primary">Chỉnh sửa</router-link>
                                </td>
                            </tr>
                        </table>
                    </div>
                </div>
                <div class="col-xs-6">
                    <h3 class="box-title">Danh sách quận huyện</h3>
                    <div id="table-district">
                        <table class="table table-hover" >
                            <tr>
                                <th>STT</th>
                                <th>Tên</th>
                                <th>Url</th>
                                <th>Giá</th>
                                <th>Chức năng</th>
                            </tr>
                            <tr v-for="item in district" :key="item.id">
                                <td>{{ item.stt }}</td>
                                <td>{{ item.name }}</td>
                                <td>{{ item.url }}</td>
                                <td>{{ item.price }}</td>
                                <td>
                                    <router-link :to="{name: 'route.admin.province.edit', params:{id: item.id}}" class="btn btn-primary">Chỉnh sửa</router-link>
                                </td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        <!-- /.box-body -->
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            api: this.$store.state.domainApi,
            dataProvince: [],
            province: [],
            district: [],
            active:0
        }
    },
    methods: {
        getDataProvince(){
            axios.get(this.api + "/admin/province",{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                }
            })
            .then(response=>{
                this.dataProvince = response.data;
                this.getProvince();
            })
        },
        search(e){
            this.province = this.dataProvince.filter(element=>(element.parent_id==0 && element.name.toLocaleLowerCase().indexOf(e.target.value.toLocaleLowerCase()) >=0 ));
        },
        getProvince(){
            this.province = this.dataProvince.filter(e=>e.parent_id==0);
        },
        viewDistrict(id){
            this.active = id;
            this.district = this.dataProvince.filter(e=>e.parent_id==id);
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        }
    },
    created() {
        this.getDataProvince();
    }
}
</script>

