table th{
    border: 1px solid #ecf0f5;
    padding: 5px;
}
table td{
    border: 1px solid #ecf0f5;
    padding: 5px;
}
#table-province{
   height: 650px;
   overflow-y: auto;
}
#table-district{
    height: 684px;
    overflow-y: auto;
}
tr.active {
    background: #ffe8e6;
}
